import L from 'leaflet';
import 'leaflet-draw';
import {featureLayer, dynamicMapLayer} from 'esri-leaflet';
import {service} from 'esri-leaflet-gp';
import {geosearch, geocodeService} from 'esri-leaflet-geocoder';
import {drawnItemsToJSON} from './exportGeoJSON';

//////////////////////////////////////////////////////////////////////////////////////////////
// workaround for leaflet marker not loading
// L.Icon.Default.imagePath = '.';
// OR
delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'), iconUrl: require('leaflet/dist/images/marker-icon.png'), shadowUrl: require('leaflet/dist/images/marker-shadow.png')});
//////////////////////////////////////////////////////////////////////////////////////////////
var map = L.map('map', {
  zoomControl: true,
  editable: true,
  doubleClickZoom: false
}).setView([
  38.3607, -75.5994
], 11);
//////////////////////////////////////////////////////////////////////////////////////////////
// basemap with zoom dependency
L.tileLayer('http://{s}.www.toolserver.org/tiles/bw-mapnik/{z}/{x}/{y}.png', {
  attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
  maxZoom: 15,
  minZoom: 7
}).addTo(map);

var image = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
  attribution: '&copy; <a href="https://osm.org/copyright">OpenStreetMap</a> contributors',
  maxZoom: 20,
  minZoom: 15
}).addTo(map);
//////////////////////////////////////////////////////////////////////////////////////////////
// add leaflet draw controls

var editableLayers = new L.FeatureGroup();
map.addLayer(editableLayers);

var MyCustomMarker = L.Icon.extend({
  options: {
    shadowUrl: null,
    iconAnchor: new L.Point(12, 12),
    iconSize: new L.Point(24, 24),
    iconUrl: require('leaflet/dist/images/marker-icon.png')
  }
});

var options = {
  position: 'topleft',
  draw: {
    polyline: {
      shapeOptions: {
        color: '#f357a1',
        weight: 10
      }
    },
    polygon: {
      allowIntersection: false, // Restricts shapes to simple polygons
      drawError: {
        color: '#e1e100', // Color the shape will turn when intersects
        message: '<strong>Oh snap!<strong> you can\'t draw that!' // Message that will show when intersect
      },
      shapeOptions: {
        color: '#bada55'
      }
    },
    circle: {
      shapeOptions: {
        color: '#315e80'
      }
    }, // Turns off this drawing tool
    rectangle: {
      shapeOptions: {
        clickable: false
      }
    },
    marker: {
      icon: new MyCustomMarker()
    }
  },
  edit: {
    featureGroup: editableLayers, //REQUIRED!!
    remove: true
  }
};

var drawControl = new L.Control.Draw(options);
map.addControl(drawControl);

map.on(L.Draw.Event.CREATED, function(e) {
  var type = e.layerType,
    layer = e.layer;

  if (type === 'marker') {
    layer.bindPopup('A popup!');
  }

  editableLayers.addLayer(layer);
});

///////////////////////////////////////////////////////////////////////////////////////////
// download function
  document.getElementById('exportButton').onclick = function(e) {
    // Extract GeoJson from featureGroup
    var data = editableLayers.toGeoJSON();
    // Stringify the GeoJson
    var convertedData = 'text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(data));
    // Create export
    document.getElementById('exportButton').setAttribute('href', 'data:' + convertedData);
    document.getElementById('exportButton').setAttribute('download', 'data.geojson');
    // activate.onClick();
    console.log('export button clicked');
  }

//////////////////////////////////////////////////////////////////////////////////////////////
// esri-leaflet-geocoder function {geosearch}
var searchControl = geosearch().addTo(map);

var results = L.layerGroup();

searchControl.on('results', function(data) {
  results.clearLayers();
  for (var i = data.results.length - 1; i >= 0; i--) {
    results.addLayer(L.marker(data.results[i].latlng));
  }
});
//////////////////////////////////////////////////////////////////////////////////////////////
// esri-leaflet-geocoder reverse geocodeService
var revGeocodeService = geocodeService();

map.on('click', function(e) {
  revGeocodeService.reverse().latlng(e.latlng).run(function(error, result) {
    L.marker(result.latlng).addTo(results).bindPopup(result.address.Match_addr).openPopup();
  });
});
//////////////////////////////////////////////////////////////////////////////////////////////
// network analist drive time poly
var gpService = service({url: "https://sampleserver1.arcgisonline.com/ArcGIS/rest/services/Network/ESRI_DriveTime_US/GPServer/CreateDriveTimePolygons", useCors: false});

var gpTask = gpService.createTask();
// set drive time parameter for analysis
gpTask.setParam("Drive_Times", "4 8");

var driveTimes = L.featureGroup();
// map.addLayer(driveTimes);

map.on('click', function(evt) {
  // document.getElementById('info-pane').innerHTML = 'working...';
  driveTimes.clearLayers();
  gpTask.setParam("Input_Location", evt.latlng)
  gpTask.run(driveTimeCallback);
});

function driveTimeCallback(error, response, raw) {
  // document.getElementById('info-pane').innerHTML = 'click on the map to calculate 1 and 2 minute drivetimes';
  driveTimes.addLayer(L.geoJSON(response.Output_Drive_Time_Polygons));
}
//////////////////////////////////////////////////////////////////////////////////////////////
//add a simple featureLayer to the map for US congressionalDistricts from MD iMAP
var criticalAreas = featureLayer({
  url: 'https://geodata.md.gov/imap/rest/services/Environment/MD_CriticalAreas/FeatureServer/0',
  simplifyFactor: 0.5,
  precision: 5,
  style: function(feature) {
    if (feature.properties.CritArea === 'Intensely Developed Area') {
      return {color: 'Crimson', weight: 2};
    } else if (feature.properties.CritArea === 'Wetland Area') {
      return {color: 'Blue', weight: 2};
    } else if (feature.properties.CritArea === 'Corporate Limit') {
      return {color: 'DarkSlateGrey', weight: 2};
    } else if (feature.properties.CritArea === 'Resource Conservation Area') {
      return {color: 'DarkOliveGreen', weight: 2};
    } else if (feature.properties.CritArea === 'Limited Development Area') {
      return {color: 'DarkOrange', weight: 2};
    } else if (feature.properties.CritArea === 'Federal Lands') {
      return {color: 'Navy', weight: 2};
    } else {
      return {color: 'Tan', weight: 2};
    }
  }
});
// map.addLayer(criticalAreas);
///////////////////////////////////////////////////////////////////////////////////////////////
//custom popup for criticalAreas
criticalAreas.bindPopup(function(layer) {
  return L.Util.template('<p>{Type}<br>{CritArea}<br>{Location}</p>', layer.feature.properties);
});
//////////////////////////////////////////////////////////////////////////////////////////////
//add a simple featureLayer to the map for Critial Area from MD iMAP
var congressionalDistricts = featureLayer({
  url: 'https://geodata.md.gov/imap/rest/services/Boundaries/MD_ElectionBoundaries/FeatureServer/0',
  simplifyFactor: 0.5,
  precision: 5,
  style: function(feature) {
    if (feature.properties.US_Representatives_Party === 'Democrat') {
      return {color: 'blue', weight: 2};
    } else if (feature.properties.US_Representatives_Party === 'Republican') {
      return {color: 'red', weight: 2};
    } else {
      return {color: 'white', weight: 2};
    }
  }
});
// map.addLayer(congressionalDistricts);
///////////////////////////////////////////////////////////////////////////////////////////////
//custom popup for congressionalDistricts
congressionalDistricts.bindPopup(function(layer) {
  return L.Util.template('<p>{Label}<br>{US_Representatives}<br>{US_Senator_1}<br>{US_Senator_2}</p>', layer.feature.properties);
});
///////////////////////////////////////////////////////////////////////////////////////////////
//dynamic map layer from ArcServer
var soilsLayer = dynamicMapLayer({url: 'https://services.arcgisonline.com/arcgis/rest/services/Specialty/Soil_Survey_Map/MapServer', opacity: 0.7});

///////////////////////////////////////////////////////////////////////////////////////////////
//control group function
var baseLayers = {
  // "Grayscale": gray,
  // "Streetmap": L.esri.basemapLayer("Streets")
};
var overlays = {
  "MD iMap Critical Areas": criticalAreas,
  "U.S. Congressional Districts": congressionalDistricts,
  "U.S. Soil Survey": soilsLayer,
  "4 - 8 min. Drive Time Analysis": driveTimes,
  "Forward/Reverse Geocoding Service": results,
  "Leaflet Draw": editableLayers
};

L.control.layers(baseLayers, overlays).addTo(map);



